import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'; 
import 'bootstrap/dist/css/bootstrap.css';

class Nav extends Component {
    render() {
        return (
            
    <nav className="navbar navbar-expand-lg sticky-top navbar-light bg-light m-auto">
        <div class="container">
        <a class="navbar-brand" href="#">
          <img src={require('./images/mediatorlogo2.png')} alt="" width="110px" height="50px" />
        </a>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarResponsive">
              <ul className="navbar-nav ml-auto">
                <li className="nav-item">
                  <a className="nav-link" href="#">Home<span className="sr-only">(current)</span></a>
                </li>
                <li className="nav-item">
                  <Link to='/about'><a className="nav-link" href="#">About us</a></Link>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="#">Countries</a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="#">Documentation</a>
                </li>
                <li className="nav-item">
                  <a className="nav-link" href="#">How it works</a>
                </li>
                <li className="nav-item">
                  <Link to='/agencylogin'><a className="nav-link" href="#">Agency</a></Link>
                </li>
                <li className="nav-item">
                  <Link to='/contact'><a className="nav-link" href="#">Contact</a></Link>
                </li>
              </ul>
           </div>
        </div>
    </nav>
        );
    }
}

export default Nav;

 
