/* SERBIAN 
APLICIRANJE
Nekoliko sekundi nakon sto aplicirate, nas algoritam proverava ponudjene poslove i momentalno Vas, putem e-mail-a, obavestava da li postoji neki posao koji se poklapa sa vasim profilom.
Takodje, ukoliko se ne Vas profil ne uklapa potpuno u trenutnu potraznju, on se salje svim agencijama koje su imale slicne potrebe u ranijem periodu i Vi time, fakticki, konkurisete odjedanput kod veceg broja agencija koje potrazuju ili su potrazivale radnike Vaseg profila. Odgovor, u ovom slucaju, od agencija mozete ocekivati u roku od nekoliko sati ili nekoliko dana od trenutka apliciranja preko MEDIATOR-a.
Ako se ne uklapate ni u kakvu trenutacnu potraznju, pozicija vase aplikacije je na aktivnom cekanju. To znaci da cim neka agencija iz svih zemalja sa kojima saradjujemo raspise konkurs i on se poklapa sa Vasim profilom, Vasa aplikacija se istog trenutka salje na razmatranje. Ukoliko u ovom krugu zaposljavanja budete usli u uzi izbor agencije, bicete obavesteni putem e-mail-a i nakon sto potvrdite da ste zainteresovani Vasa potvrda se, uz Vasu aplikaciju, prosledjuje agenciji kao verifikovana potvrda, i proces zaposljavanja pocinje. 

PROCES ZAPOSLJAVANJA
U trenutku kada Vi i agencija verifikujete sporazum otpocinje proces zaposljavanja, sto znaci da, ut tom trenutku Vi dobijate direktive od strane MEDIATOR-a a koje je poslala agencija za zaposljavanje, i eventualno potrebe za dodatnu dokumentaciju. Takodje, u ovoj fazi dobijate i datum zaposljavanja, datum polaska iz maticne drzave i ostale informacije o prevozu i smestaju.

VERIFIKACIJA
Kada je posao ugovoren - sporazum izmedju zaposlenog i agencije je verifikovan i podaci o specificnim odredbama dostavljeni, a datum i nacin odlaska utanacen, predstoji period verifikovanja koji se obavlja 7, 3 i 1 dan pred odlazak a ostvaruje se jednim klikom na verifikacionom e-mail-u koji stize na e-mail adresu sa koje je korisnik aplicirao za posao preko MEDIATOR-a.

POSLEDNJI KORAK
Nakon sto se zaposlite i otpocnete radni odnos pozeljno je da ostavite svoj komentar i ocenite MEDIATOR kako bi i drugi ljudi koji su u potrazi za poslom znali da postoji sansa i za njihovo zaposljavanje bez mnogo problema. Takodje, bilo bi dobro kada biste nam pisali o svojim novim iskustvima i sugestijama kako bismo mogli da unapredimo uslugu. 
*/